.about {

	&__wrapper {
		display: flex;
		margin-bottom: 40px;
		&--white {
			padding: 30px;
			background-color: #fff;
			border-radius: 5px;
			box-shadow: 0px 2px 10px -5px #000000; } }

	&__text p {
		margin-bottom: 20px;
		font-size: 18px; }

	&__img {
		display: flex;
		margin-right: 40px;
		box-shadow: 0px 2px 10px -5px #000000;
		img {
			border-radius: 5px;
			object-fit: cover; } }

	&__subtitle {
		text-align: center;
		font-weight: 400;
		font-family: 'Julius Sans One', sans-serif;
		font-size: 32px;
		margin-bottom: 20px; }

	&__col {
		padding: 30px;
		margin-right: 40px;
		background-color: #fff;
		border-radius: 5px;
		box-shadow: 0px 2px 10px -5px #000000;
		&:last-child {
			margin-right: 0; } }

	&__list {

		li {
			margin-bottom: 10px; } } }
