body {
    min-height: 300vh;
    background-image: linear-gradient(to bottom, rgba(#fca7ff, 0.7), rgba(#e0c3fb, 0.7), rgba(#b1e9fc, 0.7), rgba(#0386ce, 0.7));
    color: #4c4c4c;
    font-family: 'Roboto', sans-serif;
    line-height: 1.4; }
.container {
    width: 1240px;
    margin: 0 auto; }

.title {
    font-family: 'Julius Sans One', sans-serif;
    font-size: 46px;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 80px; }

.logo h1 {
    font-family: 'Julius Sans One', sans-serif;
    font-size: 24px;
    letter-spacing: 10px;
    color: white; }

.icon-insta, .icon-fb, .icon-pint {
    transition: all 0.3s ease;
    fill: #ccc; }

.icon-insta:hover, .icon-fb:hover, .icon-pint:hover {
    transform: scale(1.2); }

.icon-insta:hover {
    fill: url(#SVGID_1_); }

.icon-pint:hover {
    fill: #ca2127; }

.icon-fb:hover {
    fill: #0f92f3; }

.socials {
    display: flex;

    li {
        display: flex;
        margin-right: 20px;
        &:last-child {
            margin-right: 0; } }
    svg {
        width: 24px;
        height: 24px;
        display: flex;

        li {
            display: flex; }

        a {
            display: flex; } }

    .icon {
        background-color: #fff;
        border-radius: 6px; } }

