html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-fb {
  width: 1em;
  height: 1em;
  fill: #1777f2;
}

.icon-insta {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-pint {
  width: 1em;
  height: 1em;
  fill: #ca2127;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  min-height: 300vh;
  background-image: linear-gradient(to bottom, rgba(252, 167, 255, 0.7), rgba(224, 195, 251, 0.7), rgba(177, 233, 252, 0.7), rgba(3, 134, 206, 0.7));
  color: #4c4c4c;
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
}

.container {
  width: 1240px;
  margin: 0 auto;
}

.title {
  font-family: 'Julius Sans One', sans-serif;
  font-size: 46px;
  text-align: center;
  margin-bottom: 40px;
  margin-top: 80px;
}

.logo h1 {
  font-family: 'Julius Sans One', sans-serif;
  font-size: 24px;
  letter-spacing: 10px;
  color: white;
}

.icon-insta, .icon-fb, .icon-pint {
  transition: all 0.3s ease;
  fill: #ccc;
}

.icon-insta:hover, .icon-fb:hover, .icon-pint:hover {
  transform: scale(1.2);
}

.icon-insta:hover {
  fill: url(#SVGID_1_);
}

.icon-pint:hover {
  fill: #ca2127;
}

.icon-fb:hover {
  fill: #0f92f3;
}

.socials {
  display: flex;
}

.socials li {
  display: flex;
  margin-right: 20px;
}

.socials li:last-child {
  margin-right: 0;
}

.socials svg {
  width: 24px;
  height: 24px;
  display: flex;
}

.socials svg li {
  display: flex;
}

.socials svg a {
  display: flex;
}

.socials .icon {
  background-color: #fff;
  border-radius: 6px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}

.header__in {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.slide {
  opacity: 0.5;
  height: 600px;
  display: flex;
  margin: 30px;
  transition: all 0.4s ease;
}

.slide img {
  width: 100%;
  object-fit: cover;
  object-position: top center;
}

.slick-center .slide {
  opacity: 1;
  transform: scale(1.2);
}

.hero {
  height: 100vh;
  margin-bottom: 80px;
  box-shadow: 0px 4px 15px;
}

.hero__in {
  position: relative;
  height: 100%;
  width: 100%;
}

.hero__img {
  display: flex;
  height: 100%;
}

.hero__img img {
  width: 100%;
  object-fit: cover;
  object-position: center -150px;
  opacity: 0.5;
}

.hero__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  width: 1240px;
  font-size: 56px;
  font-family: 'Architects Daughter', cursive;
  font-weight: 400;
  color: white;
  z-index: 2;
}

.hero__title span:last-child {
  margin-left: auto;
  margin-right: 30px;
  margin-top: 20px;
  font-size: 22px;
}

.hero__overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(0, 41, 64, 0.7);
}

.about__wrapper {
  display: flex;
  margin-bottom: 40px;
}

.about__wrapper--white {
  padding: 30px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__text p {
  margin-bottom: 20px;
  font-size: 18px;
}

.about__img {
  display: flex;
  margin-right: 40px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__img img {
  border-radius: 5px;
  object-fit: cover;
}

.about__subtitle {
  text-align: center;
  font-weight: 400;
  font-family: 'Julius Sans One', sans-serif;
  font-size: 32px;
  margin-bottom: 20px;
}

.about__col {
  padding: 30px;
  margin-right: 40px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.about__col:last-child {
  margin-right: 0;
}

.about__list li {
  margin-bottom: 10px;
}
